<template>
	<div>
		<template v-if="loadingThread">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<h4 class="bold">{{ threads.title}}</h4>
						<p class="user-profile__name">{{ threads.body }}</p>
					</div>
				</div>
				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="true">Threads</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="settings" role="tabpanel" aria-labelledby="settings-tab">
						<div class="main__header">
							<div class="main__primary main__primary--inline ">
								<h3 class="main__title">Forum Thread
								</h3>
								<span v-if="loading">
									<Loader />
								</span>
							</div>
						</div>
						<section class="main-grid__row contacts contacts--list">
							<div class="contacts--list__wrapper">
								<div class="contacts--list__item head">
									<div></div>
									<p>Thread Body</p>
									<p>Actions</p>
								</div>
								<div class="contacts__body">
									<template v-if="threadLoaded && threads.thread.length">
										<ThreadItem
											v-for="thread in threads.thread"
											:key="thread.id"
											:thread="thread" />
									</template>
									<template v-else-if="threadLoaded">
										<p>No Thread found</p>
									</template>
									<template v-else>
										<p>An error occurred while loading thread, please try again.</p>
									</template>
								</div>
							</div>
						</section>
					</div>
				</div>
			</section>
		</template>
		<Pagination 
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit" />
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import { getForumThread, deleteThreadItem } from '../../utils/api/forum';
import Pagination from '../../components/Pagination';
import ThreadItem from './ThreadItem';
import eventHub from '../../utils/eventhub';
import { success, fail } from '../../utils/toast';

export default {
	name: 'ForumThread',
	data() {
		return {
			threads: [],
			loadingThread: true,
			threadLoaded: false,
			limit: 10,
			offset: 0,
			total: 0,
			baseUrl: `forum/${this.$route.params.id}/thread`,
			deletedCommentId: null,
			loading: false
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getThread();
		eventHub.$on('delete-thread', (payload) => {
			this.deletedCommentId = payload;
			this.deleteComment();
			return true;
		});
	},
	methods: {
		getThread() {
			getForumThread(this.$route.params.id, this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.threadLoaded = true;
						this.threads = response.data.data;
						return true;
					}
					this.threadLoaded = false;
					return false;
				})
				.catch((error) => {
					this.loadingThread = false;
					return error;
				})
				.finally(() => {
					this.loadingThread = false;
				});
			return true;
		},
		deleteComment() {
			this.loading = true;
			deleteThreadItem(this.$route.params.id, this.deletedCommentId)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Forum has been deleted'
						});
						const deletedThread = this.threads.thread.find((item) => {
							return item.id === this.deletedCommentId;
						});
						this.threads.thread.splice(this.threads.thread.indexOf(deletedThread), 1);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong. Please try again.'
					});
					return error;
				})
				.finally(() => {
					this.loading = false;
				});

			return true;
		}
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getThread();
		}
	},
	components: {
		Pagination,
		Loader,
		ThreadItem
	}
};
</script>

<style lang="scss" scoped>
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
	display: grid;
	grid-template-columns: 35px 1fr 250px;
	font-size: 14px;
}
.main__primary--inline {
	display: flex;
	span {
		margin-top: -20px;
	}
}
</style>
