<template>
	<div>
		<div class="contacts--list__item">
			<label class="check">
				<input type="checkbox">
				<div class="check-mark"></div>
			</label>
			<div class="contacts__info"
				:key="thread.id">
				<p class="info__name">{{ thread.body }}</p>
			</div>
			<p class="btn__row">
				<button @click="handleDelete" class="btn btn__danger">Delete</button>
				<router-link
					:to="{ name: 'forum-comment',
					params: { 
						forumId: this.$route.params.id,
						threadId: thread.id }}"
					class="btn btn__primary">
					View Comments
				</router-link>
			</p>
		</div>
	</div>
</template>

<script>
import eventHub from '../../utils/eventhub';

export default {
	name: 'ThreadItem',
	props: {
		thread: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	data() {
		return {};
	},
	methods: {
		handleDelete() {
			eventHub.$emit('delete-thread', this.thread.id);
		}
	}
};
</script>


